<!--
    Component item body tab view
-->
<template>
  <div :class="classTab()" :id="'tabBody' + tab.index">
    <GlobalErrorComponent :externalError="tab.error" :showLogout="false">
      <div class="TabViewBodyItemContainer" :id="'tabBodyContainer' + tab.index"></div>
      <div class="TabViewBodyItemComponentModals" :id="'modals' + tab.index"></div>
    </GlobalErrorComponent>
  </div>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import VueContext from "../../context/VueContext";
import GlobalErrorComponent from "../error/GlobalErrorComponent";

export default {
  extends: BaseComponent,
  name: "TabViewBodyItemComponent",
  components: {
    GlobalErrorComponent
  },
  props: {
    first: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    /**
     * Method to know tab is active
     * @returns true if active false if not
     */
    active: function() {
      return VueContext.activeTab === this.tab.index;
    },
    /**
     * Method to knwo class tab
     * @returns class for tab
     */
    classTab: function() {
      return (
        "TabViewBodyItemComponent " +
        (VueContext.activeTab === this.tab.index
          ? "TabViewBodyItemComponentActive"
          : "")
      );
    }
  }
};
</script>
<style scoped>
.TabViewBodyItemComponent {
  width: 100%;
  height: 100%;
  float: left;
  clear: left;
  box-sizing: border-box;
  display: none;
  position: relative;
  overflow-y: auto;
  padding-top: 10px;
}
.TabViewBodyItemContainer {
  width: 100%;
  height: 100%;
  float: left;
  clear: left;
  box-sizing: border-box;
}
.TabViewBodyItemComponentActive {
  display: block;
}

.TabViewBodyItemComponentModals {
  display: block;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
</style>